import { useRef, useState } from 'react';
import list from '../../client/components/js/listHome';

const Add = () => {
    const formRef = useRef();
    const [loadingState, setLoadingState] = useState(false);

    const GetRadio = () => {
        return list.map((e, index) => {
            return (
                <div className='form-check' key={index}>
                    <input className="form-check-input" type="radio" name="kategori" id="radio" value={e.link} />
                    <label className="form-check-label" htmlFor="radio">
                        {e.name}
                    </label>
                </div>
            )
        })
    }

    const Submit = async (e) => {
        setLoadingState(true)
        e.preventDefault()
        fetch('http://localhost:5000/upload', {
            method: 'POST',
            body: new FormData(formRef.current),
        }).then(res => {
            if (res.status === 200) {
                console.log('Success')
                alert('Submited');
                setLoadingState(false);
            } else {
                console.log("Status: " + res.status)
                return Promise.reject("server")
            }
        }).catch(err => console.log(err))
    }

    return (
        <div>
            <h1 className='h1 container-fluid text-center'>Add <span className=' h2 text-dark'> to Database </span></h1>
            <div className='px-2'>
                <form className=' container mb-5' onSubmit={e => Submit(e)} ref={formRef}>
                    <div className='row'>
                        <div className="mb-3 col-sm">
                            <label htmlFor="inputKode" className="form-label h5">Kode</label>
                            <input type="text" className="form-control" name='kode' id="inputKode" aria-describedby="kode" required
                                placeholder='ex. CSM1' />
                        </div>
                        <div className="mb-3 col-sm">
                            <label htmlFor="inputWarna" className="form-label h5">Warna</label>
                            <input type="text" className="form-control" name='warna' id="inputWarna" aria-describedby="warna" required
                                placeholder='ex. Putih' />
                        </div>
                    </div>

                    <div className="mb-3">
                        <label htmlFor="inputHarga" className="form-label h5">Harga</label>
                        <input step={100000} type='number' className="form-control" name='harga' id="inputHarga" aria-describedby="harga"
                            placeholder='ex. 2000000' required />
                    </div>

                    <div className='row m-0 p-0'>
                        <div className="mb-2 form-check col-sm container">
                            <label className="form-check-label h5" htmlFor="favoritCheck">Favorit</label>
                            <input type="checkbox" className="form-check-input" id="favoritCheck" name='favorit' value="true" />
                        </div>

                        <div className='mb-2 form-check col-sm p-0'>
                            <label className='h5'>Kategori</label>
                            {GetRadio()}
                        </div>
                    </div>

                    <div className="mb-3 ">
                        <label htmlFor="inputDrive" className="form-label h5" id='inputFoto_add'>Upload Foto</label>
                        <input accept='image/jpeg'
                            aria-labelledby='inputFoto_add'
                            type="file"
                            className="form-control mb"
                            name='drive' id="inputDrive" aria-describedby="drive" required multiple />
                    </div>

                    <button className="container btn btn-primary">Submit</button>
                </form>
            </div>
        </div>
    )
}

export default Add;